.signup-btn, .form-btn{
    border-radius: 13px;
    border: none;
    cursor: pointer;
    background-color: #456340;  
    color: rgb(255, 255, 255) !important;
    padding: 8px 18px;
    font-size: 14px !important;
    transition: linear 0.3s;
}

.signup-btn:hover{
    background-color: #ddffd7;  
    color: #456340 !important;
}

.form-btn:hover{
    background-color: #ddffd7;  
    color: #456340 !important;
    box-shadow: 1.5px 1.5px 5px 0px rgb(124, 124, 124);
}

button{
    outline: none;
}

.popup-content{
    border: none;
    border-radius: 5px;
    background-color: #F3F3F3 !important;
    width: 500px !important;
    box-shadow: 0px 10px 15px 0px rgb(75, 75, 75);    
}

.popup-container{
    position: relative;
}

.close-on-popup{
    position: absolute;
    right: 10px;
    top: -5px;
    cursor: pointer;
}

/*scroll bar*/
::-webkit-scrollbar-track
{
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
    border-radius: 10px;
    background-color: #F5F5F5;
}

::-webkit-scrollbar
{
    width: 8px;
    background: linear-gradient(90deg, #7d8852c7, #67946fc7);
}

::-webkit-scrollbar-thumb
{
    border-radius: 10px;
    -webkit-box-shadow: inset 0 0 6px rgba(61, 61, 61, 0.863);
    background: linear-gradient(90deg, #26291ac7, #243527c7);
}